@-webkit-keyframes scale {
    to {
        width: 0;
        height: 0;
    }
}

@keyframes scale {
    to {
        width: 0;
        height: 0;
    }
}

.dot {
    width: 10px;
    height: 10px;
    border: none;
    border-radius: 50%;
    background-color: aqua;
    display: inline-block;
    position: absolute;
    -webkit-animation: 2s ease-out 0s forwards scale;
            animation: 2s ease-out 0s forwards scale;
}


